	module.exports = {"data":{"site":{"siteMetadata":{"title":"React üéÑ","author":"Michael Chan @chantastic"}},"markdownRemark":{"id":"/Users/chan/learnreact/react.holiday/src/pages/2017/5.md absPath of file >>> MarkdownRemark","html":"<div class=\"measure\">\n<p>Some components have to be special.\nThose components get defined with <strong>class</strong>.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code><span class=\"token keyword\">class</span> <span class=\"token class-name\">ClapCounter</span> <span class=\"token keyword\">extends</span> <span class=\"token class-name\">React<span class=\"token punctuation\">.</span>Component</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token function\">render</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">return</span> <span class=\"token punctuation\">(</span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span><span class=\"token punctuation\">></span></span>\n        üëè <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>i</span><span class=\"token punctuation\">></span></span> be the first to clap<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>i</span><span class=\"token punctuation\">></span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span>\n    <span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>It‚Äôs not much to look at now.\nWe‚Äôll fill it in this week.</p>\n<h2>Some ground rules</h2>\n<p>Component classes extend <code>React.Component</code> and define a <code>render()</code> method.</p>\n<p>For comparison, here‚Äôs what <code>Greeting</code> looks like as a component class.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code><span class=\"token keyword\">class</span> <span class=\"token class-name\">Greeting</span> <span class=\"token keyword\">extends</span> <span class=\"token class-name\">React<span class=\"token punctuation\">.</span>Component</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token function\">render</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">return</span> <span class=\"token punctuation\">(</span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>h1</span><span class=\"token punctuation\">></span></span>Hello <span class=\"token punctuation\">{</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>props<span class=\"token punctuation\">.</span>name<span class=\"token punctuation\">}</span><span class=\"token punctuation\">.</span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>h1</span><span class=\"token punctuation\">></span></span>\n    <span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h2>What speshul powerz they got?!</h2>\n<p>Unlike function components, class components have an instance‚Äî<code>this</code>. This gives us <a href=\"https://reactjs.org/docs/react-component.html\">lifecycle methods and state APIs</a> for some seriously cool interactivity.</p>\n<p>Sooooooo much ahead!</p>\n<h2>Tinker with it</h2>\n<p>Use the workspace below and play with component classes.</p>\n<ul>\n<li>Convert <code>Greeting</code> to a component class and back to a function.</li>\n<li>Define a new component using class. Be sure not to forget <code>extends</code>, <code>render()</code> and <code>this</code>.</li>\n</ul>\n</div>\n<iframe src=\"https://codesandbox.io/embed/mny86kooy\" style=\"width:100%; height:500px; border:0; border-radius: 4px; overflow:hidden;\" sandbox=\"allow-modals allow-forms allow-popups allow-scripts allow-same-origin\"></iframe>","frontmatter":{"title":"Day 5: Components can use classes for special powers","date":"December 05, 2017"}}},"pathContext":{"slug":"/2017/5/"}}

